.alert {
  border-radius: 0.5rem;
  color: $white;
  font-weight: $bold;
  margin: 1rem 0;
  padding: 0.5rem 1rem;
  
  &.error {
    background-color: $red;
  }

  &.success {
    background-color: $green;
  }
}

.properties-table {
  border-collapse: collapse;
  border-spacing: 0 ;
  margin-top: 2rem;
  width: 100%;

  thead {
    background-color: $gray;

    th {
      padding: 0.5rem;
    }
  }

  img {
    width: 30rem;
  }
}