/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: 900;
  font-size: 2rem;
}

.container {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.section {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.center-content {
  max-width: 80rem;
}

.align-right {
  display: flex;
  justify-content: flex-end;
}

.w-100 {
  width: 100%;
}

.image-sm {
  width: 20rem;
}

.btn-yellow-inlineblock {
  background-color: #E08709;
  border: none;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  margin-top: 2.5rem;
  padding: 1rem 3rem;
  text-align: center;
  text-decoration: none;
}
.btn-yellow-inlineblock:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-yellow-block {
  background-color: #E08709;
  border: none;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  margin-top: 2.5rem;
  padding: 1rem 3rem;
  text-align: center;
  text-decoration: none;
}
.btn-yellow-block:hover {
  background-color: #c77808;
  cursor: pointer;
}

.btn-red-block {
  background-color: #B30200;
  border: none;
  display: block;
  color: #FFFFFF;
  font-weight: 700;
  margin-top: 2.5rem;
  padding: 1rem 3rem;
  text-align: center;
  text-decoration: none;
}
.btn-red-block:hover {
  background-color: #9a0200;
  cursor: pointer;
}

.btn-green {
  background-color: #71B100;
  border: none;
  display: inline-block;
  color: #FFFFFF;
  font-weight: 700;
  margin-top: 2.5rem;
  padding: 1rem 3rem;
  text-align: center;
  text-decoration: none;
}
.btn-green:hover {
  background-color: #619800;
  cursor: pointer;
}

.dark-mode {
  background-color: #1a1a1a;
}
.dark-mode p {
  color: #FFFFFF;
}
.dark-mode a, .dark-mode h1, .dark-mode h2, .dark-mode h3, .dark-mode h4 {
  color: #e1e1e1;
}
.dark-mode .add-cards,
.dark-mode .add-characteristics {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark-mode .add-cards .price-add,
.dark-mode .add-characteristics .price-add {
  color: #71B100;
}
.dark-mode .add-cards .add-icon,
.dark-mode .add-characteristics .add-icon {
  filter: invert(100%);
}
.dark-mode .about-us-text {
  color: #FFFFFF;
}
.dark-mode .contact-form legend,
.dark-mode .contact-form label {
  color: #e1e1e1;
}
.dark-mode .contact-form input:not([type=submit]),
.dark-mode .contact-form textarea,
.dark-mode .contact-form select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #e1e1e1;
}

.no-webp .home {
  background-image: url(../img/header.jpg);
}

.webp .home {
  background-image: url(../img/header.jpg);
}
@media (min-width: 768px) {
  .webp .home .header-content {
    min-height: 70rem;
  }
}

.header {
  background-color: #333333;
  padding: 1rem 0 3rem 0;
}
.header.home {
  background-size: cover;
  background-position: center center;
}

.header-content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.header-content h1 {
  color: #FFFFFF;
  font-weight: 700;
  line-height: 2;
  text-align: left;
  width: 60rem;
}

.footer {
  background-color: #333333;
  padding: 2.5rem 0;
  margin-bottom: 0;
}
.footer .footer-container {
  padding-top: 2.5rem;
}
.footer .navigation-main {
  opacity: 1;
  visibility: visible;
  height: auto;
}
.footer .copyright {
  color: #FFFFFF;
  text-align: center;
}

.right-menu {
  align-items: center;
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .right-menu {
    align-items: flex-end;
  }
}
.right-menu .dark-mode-btn {
  filter: invert(100%);
  margin: 3rem 0;
  width: 3rem;
}
.right-menu .dark-mode-btn:hover {
  cursor: pointer;
}
@media (min-width: 768px) {
  .right-menu .dark-mode-btn {
    margin-top: 0;
  }
}

.bar {
  align-items: center;
  display: flex;
  flex-direction: column;
  padding-top: 3rem;
}
@media (min-width: 768px) {
  .bar {
    align-items: flex-end;
    flex-direction: row;
    justify-content: space-between;
  }
}

.navigation-main {
  height: 0;
  opacity: 0;
  transition: visibility 0.3s ease-in-out, opacity 0.5s linear;
  visibility: hidden;
}
@media (min-width: 768px) {
  .navigation-main {
    display: flex;
    align-items: center;
    opacity: 1;
    visibility: visible;
  }
}
.navigation-main a {
  color: #FFFFFF;
  display: block;
  font-size: 2.2rem;
  text-align: center;
  text-decoration: none;
}
@media (min-width: 768px) {
  .navigation-main a {
    margin-right: 2rem;
    font-size: 1.8rem;
  }
  .navigation-main a.last-of-type {
    margin-right: 0;
  }
}

@media (min-width: 768px) {
  .mobile-menu {
    cursor: pointer;
    display: none;
  }
}
.mobile-menu img {
  width: 5rem;
}

.display-on {
  height: auto;
  opacity: 1;
  visibility: visible;
}

@media (min-width: 768px) {
  .about-us-icons {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.icon img {
  height: 10rem;
  text-align: center;
}
.icon h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .adds-container {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.add-cards {
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
  margin-bottom: 2rem;
}
.add-cards .add-content {
  padding: 2rem;
}
.add-cards h3, .add-cards p {
  margin: 0;
}

.price-add {
  color: #71B100;
  font-size: 3rem;
  font-weight: 700;
}

.icons-characteristics {
  display: flex;
  list-style: none;
  padding: 0;
  max-width: 40rem;
}
.icons-characteristics li {
  flex: 1;
  display: flex;
}
.icons-characteristics img {
  flex: 0 0 3rem;
  margin-right: 1rem;
}
.icons-characteristics p {
  font-weight: 900;
}

.no-webp .contact-section {
  background-image: url("../img/encuentra.jpg");
}

.webp .contact-section {
  background-image: url("../img/encuentra.webp");
}

.contact-section {
  align-items: center;
  background-position: center center;
  background-size: cover;
  display: flex;
  flex-direction: column;
  padding: 5rem 0;
}
.contact-section h2 {
  color: #FFFFFF;
  font-size: 4rem;
  font-weight: 900;
}
.contact-section p {
  color: #FFFFFF;
  font-size: 1.8rem;
}

@media (min-width: 768px) {
  .lower-section {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 2rem;
  }
}
.blog-post {
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .blog-post {
    display: grid;
    gap: 2rem;
    grid-template-columns: 1fr 2fr;
  }
}
.blog-post:last-of-type {
  margin-bottom: 0;
}

.post-text a {
  color: #000000;
  display: block;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .post-text a {
    margin: 0;
  }
}
.post-text h4 {
  margin: 0;
  line-height: 1.4;
}
.post-text h4::after {
  content: "";
  background-color: #71B100;
  display: block;
  height: 0.5rem;
  margin-top: 1rem;
  width: 15rem;
}

.info-meta span {
  color: #E08709;
}

.testimonial {
  background-color: #71B100;
  border-radius: 2rem;
  color: #FFFFFF;
  font-size: 2.4rem;
  padding: 2rem;
}
.testimonial blockquote {
  padding-left: 5rem;
  position: relative;
}
.testimonial blockquote::before {
  content: "";
  background-image: url(../img/comilla.svg);
  background-repeat: no-repeat;
  display: block;
  height: 8rem;
  left: -2rem;
  position: absolute;
  width: 6rem;
}
.testimonial p {
  color: #FFFFFF;
  text-align: right;
}

/*** Internal Pages ***/
@media (min-width: 768px) {
  .about-us-content {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}

.about-us-text blockquote {
  font-size: 2.2rem;
  font-weight: 900;
  margin: 3rem 0 0 0;
  padding-left: 1rem 0 3rem 0;
}

.contact-form legend {
  color: #333333;
  font-size: 2rem;
}

.form-fields {
  display: flex;
  flex-direction: column;
  margin: 2rem 0;
}
.form-fields label {
  font-weight: 700;
  text-transform: uppercase;
}
.form-fields input,
.form-fields select,
.form-fields textarea {
  background-color: #FFFFFF;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  padding: 0.5rem;
}

.contact-radio {
  max-width: 30rem;
  flex-direction: row;
  justify-content: space-between;
}

.alert {
  border-radius: 0.5rem;
  color: #FFFFFF;
  font-weight: 700;
  margin: 1rem 0;
  padding: 0.5rem 1rem;
}
.alert.error {
  background-color: #B30200;
}
.alert.success {
  background-color: #71B100;
}

.properties-table {
  border-collapse: collapse;
  border-spacing: 0;
  margin-top: 2rem;
  width: 100%;
}
.properties-table thead {
  background-color: #e1e1e1;
}
.properties-table thead th {
  padding: 0.5rem;
}
.properties-table img {
  width: 30rem;
}